import gsap from "gsap";

function toggleBlocksPopupMobile() {
  let switcher = $('.why__switcher');
  let container = $('.why__containers .why__container');
  let bg = $('.why__bg');
  let close = $('.why__close');

  for (let i = 0; i < Array.from(switcher).length; i++) {
    $(switcher[i]).on('click', function () {
      let id = switcher[i].getAttribute('data-id');
      let block = $('.why__containers .why__container[data-id="' + id + '"]');
      if (block.hasClass('active')) {
        bg.removeClass('active');
      } else {
        bg.addClass('active');
      }
      gsap.to(container, {opacity: 0, duration: 0.3, onComplete: () => {
        gsap.to(container, {display: 'none', duration: 0});
        gsap.to($(block).attr('data-id', $(this).attr('data-id')), {display: 'block', duration: 0, onComplete: () => {
          gsap.to($(block).attr('data-id', $(this).attr('data-id')), {opacity: 1, zIndex: 100, duration: 0.3});
        }});
      }});
    });
  }

  close.on('click', function() {
    bg.removeClass('active');
    gsap.to(container, {opacity: 0, zIndex: -1, duration: 0.3, onComplete: () => {
      gsap.to(container, {display: 'none', duration: 0});
    }});
  });
}
$(window).on('resize', function() {
  if ($(window).width() <= 1366) {
    toggleBlocksPopupMobile();
  }
});
if ($(window).width() <= 1366) {
  toggleBlocksPopupMobile();
}