import Swiper from "swiper";
import { Navigation } from 'swiper/modules';

function sliderPopupModel() {
  new Swiper('.swiper-popup-model', {
    modules: [ Navigation ],
    grabCursor: true,
    spaceBetween: 10,
    slidesPerView: 1,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
  });
}
sliderPopupModel();