import { disablePageScroll, enablePageScroll } from 'scroll-lock';

function toggleSignInPopup() {
  let button = $('.trigger-sign-in');
  let popup = $('.popup-sign-up');
  let bg = $('.popup-sign-up__bg');
  let close = $('.popup-sign-up__close, .popup-sign-up__button-success');
  let success = $('.popup-sign-up__success');
  let wrapper = $('.popup-sign-up__wrapper');
  button.on('click', function() {
    popup.addClass('active');
    bg.addClass('active');
    disablePageScroll();
  });
  close.on('click', function() {
    popup.removeClass('active');
    bg.removeClass('active');
    enablePageScroll();
    success.hide();
    wrapper.show();
  });
}
toggleSignInPopup();