import sendMail from "./sendMail.js"
import Inputmask from "inputmask";

function sendFormSignUp() {

  const validateName = (name) => {
    if (name.length >= 2 && name.length < 50) {
      return name.match(
        /^[а-яА-Я]/
      );
    }
  }

  const validatePhone = (phone) => {
    return phone.match(
      /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/
    );
  }

  const validatedName = function() {
    const name = $(this).val();

    if (validateName(name)) {
      $(this).removeClass('not-valid').addClass('filled');
    } else {
      $(this).addClass('not-valid').addClass('filled');
    }
    if (name == '') {
      $(this).removeClass('not-valid').removeClass('filled');
    }
    return false;
  }

  const validatedPhone = function() {
    const phone = $(this).val();

    if (validatePhone(phone) && phone.length > 0) {
      $(this).removeClass('not-valid');
      $(this).addClass('filled');
    } else {
      $(this).addClass('filled');
      $(this).addClass('not-valid');
    }
    if (phone == '') {
      $(this).removeClass('not-valid').removeClass('filled');
    }
    return false;
  }

  $('input[name="name"]').on('input', function() {
    validatedName.call(this);
  });
  $('input[name="phone"]').on('input', function() {
    validatedPhone.call(this);
  });

  function sender() {
    let form = $('.popup-sign-up__form');
    let button = $('.popup-sign-up__button').not('.popup-sign-up__button-success');
    let success = $('.popup-sign-up__success');
    let wrapper = $('.popup-sign-up__wrapper');
    button.on('click', function() {
      Array.from($('.popup-sign-up__form input')).forEach(function(input) {
        if ($(input).val() == '') {
          $(input).addClass('not-valid');
        } else {
          $(input).removeClass('not-valid');
        }
      });
    });
    if (form) {
      form.on('submit', function(e) {
        e.preventDefault();
        if (
          !$('.popup-sign-up__form input[name="name"]').hasClass('not-valid') &&
          !$('.popup-sign-up__form input[name="phone"]').hasClass('not-valid')) {
          sendMail(form).then(function() {
            form.get(0).reset();
            $('.popup-sign-up__form input').removeClass('not-valid');
            $('.popup-sign-up__form input').removeClass('filled');
            success.show();
            wrapper.hide();
          });
        }
      });
    }
  }
  sender();

  function inputMask() {
    let input =  $('input[type="tel"]');
    Array.from(input).forEach(function(element) {
      let mask = new Inputmask('+7 (999) 999-99-99');
      mask.mask(element);
    });
  }
  inputMask();
}
sendFormSignUp();